<template>
  <div class="onlineCustomerService">
     <el-button @click="sendMessage">发送</el-button>
    <iframe
      src="http://localhost:8000/#/onlineCustomerService/detail"
      name="sendMessage"
      id="iframe"
      title=""
      frameborder="0"
      scrolling="no"
    ></iframe>
  </div>
</template>

<script>
// import Interface from '../base/mixin/talk-manage';
export default {
  components: {
  },
  name: "onlineCustomerService",
  //   mixins: [Interface],
  data() {
    return {

    }
  },
  mounted() {
    window.onload = () => {
      this.sendMessage()
    }
  },
  methods: {
    sendMessage() {
      document.getElementById('iframe').contentWindow.postMessage({
        msg: {
          clientId: "",
          telPhone: "18933272317",
        }
      },
        "http://localhost:8000/#/onlineCustomerService/detail")
    }
  },
}
</script>

<style scoped lang="scss">
.onlineCustomerService {
  width: 100%;
  height: 100%;
  #iframe {
    width: 100%;
    height: 100%;
  }
}
</style>